import React from "react";
import { Link } from "react-router-dom";
import {
  TextHeroContainer,
  TextContent,
  TextSecondary,
  TextPrimary,
  TextTertiary,
  Button,
} from "./textHeroStyled";

const TextHero = () => {
  return (
    <TextHeroContainer>
      <TextContent>
        <TextSecondary>MANTN TU</TextSecondary>
        <TextPrimary>LOOK</TextPrimary>
        <TextPrimary>PERFECTO</TextPrimary>
        <TextTertiary>
          En TuBarbera nos especializamos en los gustos y necesidades del
          hombre. Somos una barbera para caballeros
        </TextTertiary>
      </TextContent>
      <Button>
        {" "}
        <Link to="/Reserva"> RESERVA</Link>
      </Button>
    </TextHeroContainer>
  );
};
export default TextHero;
